/* global Office */

Office.onReady(() => {
  // If needed, Office.js is ready to be called.
});

/**
 * Shows a notification when the add-in command is executed.
 * @param event {Office.AddinCommands.Event}
 */
function action(event) {
  console.log("action done")
    // Note: In order for you to successfully add a category,
    // it must be in the mailbox categories master list.
    Office.context.mailbox.masterCategories.getAsync(function (asyncResult) {
      if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
        const masterCategories = asyncResult.value;
        if (masterCategories && masterCategories.length > 0) {
          // Grab the first category from the master list.
          const categorys = [masterCategories[0].displayName];
          console.log(masterCategories);
          const categoryToAdd = ["red"];
          Office.context.mailbox.item.categories.addAsync(categoryToAdd, function (asyncResult) {
            if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
              console.log(`Successfully assigned category '${categoryToAdd}' to item.`);
              event.completed();
            } else {
              console.log("categories.addAsync call failed with error: " + asyncResult.error.message);
              event.completed();
  
            }
          });
        } else {
          console.log(
            "There are no categories in the master list on this mailbox. You can add categories using Office.context.mailbox.masterCategories.addAsync."
          );
          event.completed();
  
        }
      } else {
        console.error(asyncResult.error);
        event.completed();
  
      }
    });
  event.completed();
}

// Register the function with Office.
Office.actions.associate("action", action);
